body {
    background-color: black;

}

#header {
    margin-top: -10px;
    height: 65px;
    background: rgb(36, 36, 36);
    scale: 0.8;
    border-radius: 10px;
    animation-iteration-count: 1;
    animation-duration: 1s;
    animation-name: header-anim;
}

a {
display: inline-block;
}

#button1 {
    width: 50px;
    height: 50px;
    background: rgb(74, 74, 74);
    border-radius: 10px;
    margin-left: 10px;
    margin-top: 7px;
    cursor: pointer;
    
}
.vkimg{
    width: 40px;
    height: 25px;
    margin-left: 5px;
    margin-top: 10px;
    margin-bottom: 20px;
}

#button2{
    width: 50px;
    height: 50px;
    background: rgb(74, 74, 74);
    border-radius: 10px;
    margin-left: 10px;
    cursor: pointer;
    
}
.dsimg{
    width: 40px;
    height: 40px;
    margin-top: 5px;
    margin-left: 5px;
    opacity: 0.6;
}
#icon2{
    margin-top: -59px;
    margin-left: 60px;
}

@keyframes header-anim{
    0%{
        opacity: 0;
    }
    10%{
        opacity: 0.1;
    }
    20%{
        opacity: 0.2;
    }
    30%{
        opacity: 0.3;
    }
    40%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    60%{
        opacity: 0.6;
    }
    70%{
        opacity: 0.7;
    }
    80%{
        opacity: 0.8;
    }
    90%{
        opacity: 0.9;
    }
    100%{
        opacity: 1;
    }
}

